body {
  height: 100%;
  background-image: url("bg.60841ea0.jpg");
  background-repeat: no-repeat;
  background-size: auto;
  font-family: Roboto, sans-serif;
}

.sidebar {
  z-index: 1;
  width: 120px;
  height: 100%;
  -webkit-backdrop-filter: blur(6px);
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  background: rgba(10, 10, 10, .65);
  border-right: 2px solid rgba(255, 255, 255, .09);
  transition: all .4s;
  position: relative;
  box-shadow: 0 8px 32px #020418;
}

.sidebar.open {
  width: 360px;
}

.sidebar .logo {
  width: 100%;
  height: 240px;
  align-items: center;
  justify-items: center;
  padding: 20px 0;
  display: grid;
}

.sidebar .logo img {
  width: 56px;
  transition: all .4s;
}

.sidebar.open .logo img {
  width: 96px;
  fill: #fff;
}

.sidebar .logo h3 {
  color: #fff;
  font-variant: small-caps;
  pointer-events: none;
  opacity: 0;
  margin-top: 12px;
  font-size: 36px;
  scale: 0;
}

.sidebar.open .logo h3 {
  opacity: 1;
  transition: all .4s .2s;
  scale: 1;
}

.sidebar .nav__title {
  color: #dadada;
  pointer-events: none;
  opacity: 0;
  margin: 40px 0 18px;
}

.sidebar.open .nav__title {
  opacity: 1;
  transition: all .4s .2s;
}

.sidebar nav {
  padding: 0 30px;
}

.sidebar nav .nav_item {
  cursor: pointer;
  width: 100%;
  height: 56px;
  color: #fff;
  border-radius: 4px;
  justify-content: flex-start;
  align-items: center;
  margin: 8px 0;
  padding: 0 16px;
  transition: all .3s;
  display: flex;
}

.sidebar nav .nav_item.active {
  background: #ff328e !important;
}

.sidebar nav .nav_item:hover {
  background: rgba(255, 255, 255, .1);
}

.sidebar nav .nav_item i {
  font-size: 26px;
}

.sidebar nav .nav_item span {
  opacity: 0;
  pointer-events: none;
  margin-left: 8px;
  font-size: 18px;
}

.sidebar.open nav .nav_item span {
  opacity: 1;
  pointer-events: visible;
  transition: all .4s .2s;
}

.sidebar hr {
  width: 100%;
  height: 2px;
  opacity: 0;
  background: rgba(255, 255, 255, .1);
  border-radius: 3px;
  margin: 40px 0 50px;
}

.sidebar.open hr {
  opacity: 1;
  transition: all .4s;
}

.sidebar .toggle {
  cursor: pointer;
  color: #fff;
  text-align: center;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0) 50%, rgba(10, 10, 10, .65) 50%);
  border-radius: 50%;
  padding: 2px 0 2px 2px;
  font-size: 38px;
  line-height: 50%;
  position: absolute;
  top: 180px;
  right: -20px;
}

.sidebar.open .toggle {
  transform: translateY(45px);
}

.sidebar.toggle i {
  transition: all .4s linear;
}

.sidebar.open .toggle i {
  transform: rotateY(180deg);
}

.container {
  width: 800px;
  transition: all .4s;
  position: absolute;
  top: 0;
  left: 100px;
}

.container.move {
  left: 250px;
}

.our_team {
  opacity: 0;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-left: 150px;
  transition: all .2s;
  display: flex;
  transform: translateY(16px);
}

.our_team.visible {
  opacity: 1;
  transform: translateY(0);
}

.team_card {
  color: #fff;
  width: 260px;
  height: 320px;
  margin-top: 70px;
  position: relative;
}

.team_card .card_inner {
  text-align: center;
  width: 100%;
  height: 100%;
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  background: rgba(255, 255, 255, .05);
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  display: flex;
  box-shadow: 0 0 20px rgba(0, 0, 0, .2);
}

.team_card .circle_left {
  pointer-events: none;
  width: 120px;
  height: 120px;
  background: linear-gradient(#e524c3, #8626b2);
  border-radius: 50%;
  animation: move-up 2s ease-in infinite alternate;
  position: absolute;
  top: -50px;
  left: -50px;
}

.team_card .circle_right {
  pointer-events: none;
  width: 180px;
  height: 180px;
  background: linear-gradient(#d64612, #e48307);
  border-radius: 50%;
  animation: move-down 2s ease-in infinite alternate;
  position: absolute;
  bottom: -86px;
  right: -70px;
}

@keyframes move-up {
  to {
    transform: translateY(-10px);
  }
}

@keyframes move-down {
  to {
    transform: translateY(10px);
  }
}

.team_card .profile {
  transition: all .4s ease-out;
  transform: translateY(20px);
}

.team_card:hover .profile {
  transform: translateY(0);
}

.team_card .profile img {
  width: 180px;
  height: 180px;
  border: 12px solid rgba(255, 255, 255, .05);
  border-radius: 50%;
}

.team_card .social_icon {
  margin-top: 20px;
}

.team_card .social_icon i {
  cursor: pointer;
  opacity: 0;
  margin: 0 8px;
  font-size: 22px;
  transition: all .2s;
  transform: translateY(16px);
}

.team_card:hover .social_icon i {
  opacity: 1;
  transform: translateY(0);
}

.team_card .social_icon i:hover {
  color: #ff328e;
}

.team_card .social_icon i:nth-child(1) {
  transition-delay: .1s;
}

.team_card .social_icon i:nth-child(2) {
  transition-delay: .2s;
}

.team_card .social_icon i:nth-child(3) {
  transition-delay: .3s;
}

.team_card .social_icon i:nth-child(4) {
  transition-delay: .4s;
}

.hero {
  opacity: 0;
  margin-left: 150px;
  transition: all .2s;
  transform: translateY(16px);
}

.hero.on {
  opacity: 1;
  transform: translateY(0);
}

.hero h1 {
  text-align: center;
  color: #f0f8ff;
  margin-top: 30px;
}

.hero p {
  text-align: justify;
  color: #f0f8ff;
  margin-top: 50px;
  font-size: 10px;
  font-weight: 200;
}

